'use client';

import React, { useState } from 'react';
import { signIn, useSession } from 'next-auth/react';
import { Button } from '@/shared/components';

export function HomePage() {
  const { data: session, status } = useSession();
  const [investorId, setInvestorId] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  
  if (status === 'loading') {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-teal-400 mx-auto mb-4"></div>
          <p className="text-gray-300">Loading...</p>
        </div>
      </div>
    );
  }
  
  if (session) {
    // Redirect based on user role
    const user = session.user as { role: 'manager' | 'investor'; investorId?: string };
    if (user.role === 'manager') {
      window.location.href = '/dashboard';
    } else {
      window.location.href = '/investor';
    }
    return null;
  }

  const handleInvestorLogin = async () => {
    if (!investorId.trim()) return;
    
    setIsLoading(true);
    try {
      // For now, we'll use a simple approach
      // In the full implementation, this would validate the investor ID
      await signIn('google', { 
        callbackUrl: `/investor?investorId=${investorId}` 
      });
    } catch (error) {
      console.error('Login error:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleManagerAccess = async () => {
    setIsLoading(true);
    try {
      await signIn('google', { 
        callbackUrl: '/dashboard' 
      });
    } catch (error) {
      console.error('Login error:', error);
    } finally {
      setIsLoading(false);
    }
  };
  
  return (
    <div className="min-h-screen bg-gray-900 flex flex-col items-center justify-center p-4 relative">
      {/* WAGMI Logo and Tagline */}
      <div className="text-center mb-12">
        <h1 className="text-6xl font-bold text-teal-400 mb-2 wagmi-glow">
          WAGMI
        </h1>
        <p className="text-gray-300 text-lg">We're All Gonna Make It</p>
      </div>

      {/* Investor Login Card */}
      <div className="bg-gray-800 rounded-2xl p-8 w-full max-w-md shadow-2xl border border-gray-700">
        <h2 className="text-2xl font-semibold text-white text-center mb-6">
          Investor Login
        </h2>
        
        <div className="space-y-6">
          <div>
            <label htmlFor="investorId" className="block text-sm font-medium text-gray-300 mb-2">
              Investor ID
            </label>
            <input
              id="investorId"
              type="text"
              value={investorId}
              onChange={(e) => setInvestorId(e.target.value)}
              placeholder="Enter your Investor ID"
              className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-teal-400 focus:border-transparent transition-all duration-200"
              onKeyPress={(e) => e.key === 'Enter' && handleInvestorLogin()}
            />
          </div>
          
          <Button
            onClick={handleInvestorLogin}
            disabled={!investorId.trim() || isLoading}
            className="w-full bg-teal-500 hover:bg-teal-600 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoading ? 'Accessing...' : 'Access Portfolio'}
          </Button>
          
          <p className="text-sm text-gray-400 text-center">
            Don't have your Investor ID? Contact your fund manager for access credentials.
          </p>
        </div>
      </div>

      {/* Manager Access Button */}
      <Button
        onClick={handleManagerAccess}
        disabled={isLoading}
        className="absolute bottom-8 right-8 bg-teal-500 hover:bg-teal-600 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
      >
        Manager Access
      </Button>
    </div>
  );
}
